import { ReedeemCouponComponent } from './promotion-core/reedeem-coupon/reedeem-coupon.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PromotionCoreComponent } from './promotion-core/promotion-core.component';


const routes: Routes = [
  {
    path: '',
    component: PromotionCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'redeem-coupon'
      },
      {
        path: 'redeem-coupon',
        component: ReedeemCouponComponent,
        data: {title: 'Coupon', breadcrumb: 'Redeem Coupon'}
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PromotionsRoutingModule { }
